import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {Member} from '../../../../../core/models/member';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-committee-member-modal',
  templateUrl: './committee-member-modal.component.html',
  styleUrls: ['./committee-member-modal.component.scss']
})
export class CommitteeMemberModalComponent implements OnInit {

  @Output() add: EventEmitter<any> = new EventEmitter();

  committeeMembers: Member[] = [];

  constructor(private activeModal: NgbActiveModal) { }

  ngOnInit() {
  }

  dismiss() {
    this.activeModal.dismiss();
  }

  onSelectMember(member: Member) {
    if (this.committeeMembers.indexOf(member) === -1) {
      this.committeeMembers.push(member);
    }
  }

  save() {
    this.add.emit(this.committeeMembers);
    this.activeModal.dismiss();
  }

}
