import {Component, OnInit} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {CommitteeService} from '../../../../../core/services/committee.service';
import {Committee} from '../../../../../core/models/committee';
import {NotifierService} from 'angular-notifier';

@Component({
  selector: 'app-activity-overview',
  templateUrl: './edit-committee.component.html',
  styleUrls: ['./edit-committee.component.scss']
})
export class EditCommitteeComponent implements OnInit {

  committee: Committee;

  constructor(
    private committeeService: CommitteeService,
    private route: ActivatedRoute,
    private router: Router,
    private notifierService: NotifierService) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.committeeService.getCommittee(params.id).subscribe(
        committee => {
          this.committee = committee;
        });
    });
  }

  onSave(committee: Committee) {
    this.committeeService.updateCommittee(committee).subscribe(
      () => {
        this.router.navigate(['/admin/committees']);
        this.notifierService.notify('default', `Committee updated successfully`);
      },
      error => {
        console.log('Error while editing Committee', error);
        this.notifierService.notify('error', `Could not update committee, try again later`);
      }
    );
  }

}
